import React, { Component } from 'react';
import lodash from 'lodash';

import ShowNotification from '../components/ShowNotification';

export default class MessageQuickForm extends Component {
	defaults = {
		message: '',
		class_id: '',
		stream_id: ''
	};

	state = {error: {}, loading: false, classes: null, data: {...this.defaults}};

	componentDidMount() {
		axios.get('/api/setup/classes').then(res=>{
			this.setState({classes: res.data.data});
		})
	}

	componentDidUpdate(prevProps,prevState) {
		if(this.props.edit){
			if(!lodash.isEqual(this.props.data, prevProps.data)){
				this.setState({data: {...this.state.data,...this.props.data}})
			}
		}
		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: {...this.state.error,...this.props.error}})
		}
	}

	inputChanged = (event) => {
		let field = event.target.name;
		let data = this.state.data;
		data[field] = event.target.value;
		if(field == 'class_id')
			data['stream_id'] = '';
		this.setState( {data} );
	}
	

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	submit = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			ShowNotification("Message Sent Successfully", "success", "nc-check-2");
		}, error => {
			this.setState({error: error.response.data});
			return Promise.reject(error.response.data);
		}).finally(_=>this.setState({loading:false}));
	}

	render() {
		let error = this.state.error || {};
		let messageInvalid='', classIdInvalid='', streamIdInvalid='';
		if(error.errors) {
			messageInvalid = error.message ? 'is-invalid' : '';
			classIdInvalid = error.class_id ? 'is-invalid' : '';
			streamIdInvalid = error.stream_id ? 'is-invalid' : '';
		}

		let {classes} = this.state;
    let streams = null;
    if(classes) {
      let selectedClass = classes.find( (cls) => cls.id == this.state.data.class_id );
      if(selectedClass) {
        streams = selectedClass.streams;
      }
    }

		return <div className="container-fluid" ref={this.elRef} >
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Message Details</h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					      <div className="form-group">
					        <label>Message</label>
					        <textarea value={this.state.data.message} onChange={this.inputChanged} name="message" placeholder="Enter message" className={`form-control ${messageInvalid}`} />
					        {messageInvalid && error.errors.message.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
					      </div>

					      <div className="row">
						      <div className="form-group col-sm-6">
						        <label>Class</label>
						        <select value={this.state.data.class_id} onChange={this.inputChanged} name="class_id" className={`form-control ${classIdInvalid}`} size="8" >
						        	<option value="">All Classes</option>
	                    {classes && classes.map( cls => <option key={cls.id} value={cls.id}>{cls.name}</option>)}
	                  </select>
						        {classIdInvalid && error.errors.class_id.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>

						      <div className="form-group col-sm-6">
						        <label>Stream</label>
						        <select disabled={!this.state.data.class_id} value={this.state.data.stream_id} onChange={this.inputChanged} name="stream_id" className={`form-control ${streamIdInvalid}`} size="8" >
						        	<option value="">All Streams</option>
	                    {streams && streams.map( stream => <option key={stream.id} value={stream.id}>{stream.name}</option>)}
	                  </select>
						        {streamIdInvalid && error.errors.stream_id.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
					      </div>

					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SEND" disabled={this.state.loading} className="btn btn-success" />
			          </div>
			          <div className="col-sm-2">
			            <input onClick={this.props.close} type="button" value="CLOSE" disabled={this.state.loading} className="btn btn-warning" />
			          </div>
					    </div>
					  </div>


					  


					</div>
		    </div>
		  </div>
		</div>

	}
}