import React, { Component } from 'react';
import { Link, Route } from 'react-router-dom';
import axios from 'axios';
import AnimatedSwitch from '../components/animated-switch';

import MessageQuickForm from '../components/MessageQuickForm';
import MessageBulkForm from '../components/MessageBulkForm';

import TodForm from '../components/TodForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

export default class MessagePages extends Component {
    render() {
        let match = this.props.match;
        return <AnimatedSwitch>
            <Route path={`${match.path}/quick`} render={(props) => <MessageQuick {...props} /> } />
            <Route path={`${match.path}/bulk`} render={(props) => <MessageBulk {...props} /> } />
            <Route exact path={`${match.path}`} render={(props) => <Messages {...props} /> } />
        </AnimatedSwitch>
    }
}

export class Messages extends Component {
    constructor(props) {
        super(props);
        let self = this;
        window.todListActionEdit = this.edit;
        this.state = {};
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            order: [[0, 'desc']],
            serverSide: true,
            processing: true,
            ajax: function(params, callback, settings) {
                axios.get('/api/messages', {params: {params}}).then(response=>{
                    callback(response.data);
                })
            },
            columns: [
                { data: 'created_at', name: 'created_at', 'class': 'text-center', render: (data,type,row) => (new Date(data)).toDateString() },
                { data: 'message', name: 'message', 'class': 'text-center'},
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    if(data.recipients) {
                        let list = "<ul>";
                        let recipients = JSON.parse(data.recipients.replace(/\&quot;/g,'"'));
                        recipients.forEach( r => list += `<li>${r}</li>` );
                        list += "</ul>";
                        return list;
                    } else {
                        let group = '';
                        if(data.class_id) {
                            let cls = self.state.classes ? self.state.classes.find(cls => cls.id == data.class_id) : null;
                            group = cls ? cls.name : data.class_id;
                            group += data.stream_id ? ` ${cls ? cls.streams.find(s => s.id == data.stream_id).name : data.stream_id}` : ' All Streams';
                        } else {
                            group = 'All Classes';
                        }
                        return group;
                    }
                }}
            ],
        };
    }

    componentDidMount() {
        axios.get('/api/setup/classes').then(res=>{
            this.setState({classes: res.data.data}, ()=> this.dataTable.current.draw() );
        })
    }

	render() {
		let match = this.props.match;
		return <div className="container-fluid">
            <div className="row">
                <div className="col-md-12">
                    <span className="pull-right">
                        <Link to={`${match.url}/bulk`} className="btn btn-success details"><i className="fa fa-comments"></i> Bulk SMS</Link>
                        &nbsp;
                        <Link to={`${match.url}/quick`} className="btn btn-info details"><i className="fa fa-comment"></i> Quick SMS</Link>
                    </span>
                </div>
            </div>
            <DataTable ref={this.dataTable} title="Sent Messages" config={this.dataTableConfig}>
                <th>Date</th>
                <th>Message</th>
                <th>recipients</th>
            </DataTable>
		</div>

	}
}

export class MessageQuick extends Component {
    state = { error: {} };
    submit = (data) => {
        return axios.post('/api/messages', data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/messages');
    }
    render() {
        return <MessageQuickForm error={this.state.error} submit={this.submit} close={this.close} />
    }
}

