import React, { Component } from 'react';
import lodash from 'lodash';

import ShowNotification from '../components/ShowNotification';

export default class MessageQuickForm extends Component {
	defaults = {
		message: '',
		recipients: ['']
	};

	state = {error: {}, loading: false, data: {...this.defaults}};

	inputChanged = (event) => {
		let field = event.target.name;
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}

	studentAdmChanged = (event) => {
		let index = parseInt(event.target.name);
		let value = event.target.value
		let data = this.state.data;
		data.recipients[index] = value;

		if(value) {
			event.target.onblur = () => {}; //if value remove onblur event function
			if(data.recipients.length == (index + 1)){
				data.recipients.push('');
			}
		} else {
			//if value is empty set an onblur event handler that will remove the input once it losses focus
			event.target.onblur = () => {
				let data = this.state.data;
				data.recipients.splice(index,1);
				this.setState({data});
			}
		}
		this.setState({data});
	}

	save = () => {
		this.setState({loading: true});
		this.submit().then(()=>this.props.close());
	}

	submit = () => {
		let data = this.state.data;
		if(data.recipients.length > 1){
			data.recipients.splice(data.recipients.length-1); //remove last item which is always an empty string
		}
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			ShowNotification("Message Sent Successfully", "success", "nc-check-2");
		}, error => {
			this.setState({error: error.response.data});
			return Promise.reject(error.response.data);
		}).finally(_=>this.setState({loading:false}));
	}

	render() {
		let error = this.state.error || {};
		let messageInvalid='', admissionInvalid='';
		if(error.errors) {
			messageInvalid = error.message ? 'is-invalid' : '';
			admissionInvalid = error.admission_no ? 'is-invalid' : '';
		}

		let recipients = this.state.data.recipients;

		return <div className="container-fluid" ref={this.elRef} >
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Message Details</h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					      <div className="form-group">
					        <label>Message</label>
					        <textarea value={this.state.data.message} onChange={this.inputChanged} name="message" type="text" placeholder="Enter message" className={`form-control ${messageInvalid}`} />
					        {messageInvalid && error.errors.message.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
					      </div>

		            <div className="form-group row">
		              <label className="col-sm-2 col-form-label">Student Admissions:</label>
		              <div className="col-10">
		                {recipients.map((value,index) => <input key={index} 
		                	value={this.state.data.recipients[index]} 
		                	onChange={this.studentAdmChanged} 
		                	name={index} type="text" 
		                	placeholder="Admission Number" 
		                	className={`form-control ${admissionInvalid}`} /> )
		                }
		              </div>
		            </div>

					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SEND" disabled={this.state.loading} className="btn btn-success" />
			          </div>
			          <div className="col-sm-2">
			            <input onClick={this.props.close} type="button" value="CLOSE" disabled={this.state.loading} className="btn btn-warning" />
			          </div>
					    </div>
					  </div>

					</div>
		    </div>
		  </div>
		</div>

	}
}