import React, { Component } from 'react';
import lodash from 'lodash';
import { inject } from 'mobx-react';

import ShowNotification from '../components/ShowNotification';
import DateInput from '../components/DateInput';
import confirmAction from '../utils/confirmAction';

@inject('teacherStore')
export default class TeacherForm extends Component {
	defaults = {
		start: '',
		end: '',
		teacher_id: ''
	};

	state = {error: {}, loading: false, teachers: null, data: {...this.defaults}};

	componentDidMount() {
    this.props.teacherStore.list({},true).then(teachers=>{
    	this.setState({teachers});
    });
	}

	componentDidUpdate(prevProps,prevState) {
		if(!lodash.isEqual(this.props.data, prevProps.data)){
			this.setState({data: {...this.state.data,...this.props.data}})
		}
		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: {...this.state.error,...this.props.error}})
		}
	}

	inputChanged = (field,event) => {
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}
	
	saveContinue = () => {
		this.submit().then(()=>{
			this.setState({data: {...this.defaults},error: {}});
		})
	}

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	apply = () => {
		this.submit().then(()=>{
			this.setState({error: {}});
		})
	}

	submit = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			if(this.props.edit) {
				ShowNotification("Teacher Duty Successfully Edited", "success", "nc-check-2");
			}else {
				ShowNotification("Teacher Duty Created Successfully", "success", "nc-check-2");
			}
		}, error => {
			this.setState({error: error});
			return Promise.reject(error);
		}).finally(_=>this.setState({loading:false}));
	}

	delete = () => {
    confirmAction( () => {
      return this.props.delete().then(res=>{
        this.props.close();
        return {success: 'Deleted!', succes_text: 'Duty was deleted'};
      }, error => {
        throw {error: "Duty couldn't be deleted."};
      })
    }, {loading_title: 'Deleting...', loading_text: 'Deleting Duty', canceled: "Deleting Duty Operation Cancelled", consequence: 'Duty will be deleted'});
  }

  cancel = () => {
      let cancelAsync = async () => this.props.cancel();
      if(lodash.isEmpty(this.props.data) || (!this.props.data.__meta_pending && lodash.isEqual(this.props.data, this.state.data))) {
          this.props.close();
          return;
      }
      confirmAction( () => {
          return cancelAsync().then(res=>{
              this.props.close();
              return {success: 'Cancelled!', succes_text: 'Teacher on Duty entry was cancelled'};
          }, error => {
              throw {error: "Teacher on Duty entry couldn't be removed."};
          })
      }, {loading_title: 'Deleting...', loading_text: 'Cancelling entry', canceled: "", consequence: 'Entered information will be discarded'});
  }

  handleSubmit = (e) => {
		e.preventDefault();
		this.submitAction();
	}

  chooseAction = (e) => {
		switch(e.target.name) {
			case 'apply':
				this.submitAction = this.apply;
				break;
			case 'save':
				this.submitAction = this.save;
				break;
			case 'save_continue':
				this.submitAction = this.saveContinue;
				break;
		}
	}

	render() {
		let error = this.state.error || {};
		let startInvalid='', endInvalid='', teacherInvalid='';
		if(error.errors) {
			startInvalid = error.errors.start ? 'is-invalid' : '';
			endInvalid = error.errors.end ? 'is-invalid' : '';
			teacherInvalid = error.errors.teacher_id ? 'is-invalid' : '';
		}

		return <div className="container-fluid">
			{this.props.edit && <div className="row">
          <div className="col-md-12">
            <span className="pull-right">
              <button type="button" className="btn btn-danger" onClick={this.delete} >Delete</button>
            </span>
          </div>
      </div>}
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Teacher Details</h4>
					  </div>
					  <div className="card-body ">
					    <form onSubmit={this.handleSubmit}>
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					      <div className="form-group">
					        <label>Starts on</label>
					        <DateInput value={this.state.data.start} onChange={this.inputChanged.bind(this,'start')} type="text" placeholder="Starts on" className={`form-control ${startInvalid}`} required />
					        {startInvalid && error.errors.start.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					      <div className="form-group">
					        <label>Ends on</label>
					        <DateInput value={this.state.data.end} onChange={this.inputChanged.bind(this,'end')} type="text" placeholder="End on" className={`form-control ${endInvalid}`} required />
					        {endInvalid && error.errors.end.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					      <div className="form-group">
					        <label>Teacher</label>
					        <select value={this.state.data.teacher_id} onChange={this.inputChanged.bind(this,'teacher_id')} name="gender" className={`form-control ${teacherInvalid}`} required >
					        	<option value="">Select Teacher</option>
                    {this.state.teachers && this.state.teachers.map( teacher => <option key={teacher.id} value={teacher.id}>{teacher.firstname} {teacher.lastname}</option>)}
                  </select>
					        {teacherInvalid && error.errors.teacher.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					      <div className="row">
						      <div className="col-sm-2">
				            <input onClick={this.chooseAction} type="submit" name="save" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
				          </div>
							  	{this.props.edit ? <div className="col-sm-2">
				            	<input onClick={this.chooseAction} type="submit" name="apply" value="APPLY" disabled={this.state.loading} className="btn btn-primary" />
				          	</div> :
				          	<div className="col-sm-3">
				            	<input onClick={this.chooseAction} type="submit" name="save_continue" value="SAVE AND CONTINUE" disabled={this.state.loading} className="btn btn-primary" />
				          	</div>
				          }
				          <div className="col-sm-2">
				            <input onClick={this.cancel} type="button" value="CANCEL" disabled={this.state.loading} className="btn btn-warning" />
				          </div>
						    </div>

					    </form>
					  </div>
					  <div className="card-footer ">
					  </div>


					  


					</div>
		    </div>
		  </div>
		</div>

	}
}